import "./scss/IphoneComponent.scss";
import "./scss/reset.scss";
import "./scss/responsive.scss";
import { Breadcrumb } from "antd";
import { HomeOutlined } from "@ant-design/icons";
import NavComponent from "./NavComponent";
import FooterComponent from "./FooterComponent";
import banner1 from "../assets/img/danh mc iphone ipad t7-10.jpg";
import banner2 from "../assets/img/Danh mc PC.jpg";
import iconComparision from "../assets/img/icons/comparison.png";
import { Carousel } from "antd";
import { fetchAllProduct } from "../sevices/product";
import { useEffect, useState } from "react";
import filter1 from "../assets/img/Image-Standard.png";
import filter2 from "../assets/img/Image-Standard-1.png";
import user from "../assets/img/default-avatar_120.jpg";
import { Col, Divider, Row } from "antd";
import iconStar from "../assets/img/icons/icon-star-1.png";
import { Input } from "antd";
import { Button, Space } from "antd";
import { Link } from "react-router-dom";
const IphoneComponent = () => {
  const [listProduct, setListProducts] = useState([]);
  useEffect(() => {
    getProducts();
  }, []);
  //call api
  const getProducts = async () => {
    let res = await fetchAllProduct();
    console.log("check res new:", res);
    if (res && res.products) {
      setListProducts(res.products);
    }
  };
  return (
    <>
      <NavComponent />
      <div className="container product-page">
        <Breadcrumb
          className="Breadcrumb"
          items={[
            {
              href: "./",
              title: <HomeOutlined />,
            },
            {
              href: "/iphone",
              title: (
                <>
                  <span>iPhone</span>
                </>
              ),
            },
          ]}
        />
      </div>
      <div className="home-page-product">
        <div className="container list-item-product">
          <div className="banner-product">
            <h1>iPhone</h1>
            <div className="banner-auto">
              <Carousel autoplay>
                <div className="img-banner">
                  <a href="/">
                    <img src={banner1} alt="" />
                  </a>
                </div>
                <div className="img-banner">
                  <a href="/">
                    <img src={banner2} alt="" />
                  </a>
                </div>
              </Carousel>
            </div>
          </div>
          <div className="list-cate-product-all">
            <div className="list-width-product">
              <div className="list-cate-product">
                <div className="item-cate active">
                  <a href=""> Tt c </a>
                </div>
                <div className="item-cate">
                  <a href=""> iPhone 14 series </a>
                </div>
                <div className="item-cate">
                  <a href=""> iPhone 13 series </a>
                </div>
                <div className="item-cate">
                  <a href=""> iPhone 12 series </a>
                </div>
                <div className="item-cate">
                  <a href=""> iPhone 11 series </a>
                </div>
                <div className="item-cate">
                  <a href=""> iPhone SE </a>
                </div>
              </div>
            </div>
            <div className="compare-product">
              <a href="">
                <img src={iconComparision} alt="" />
                So snh iPhone
              </a>
            </div>
          </div>
          <div className="list-product">
            <div className="category-page">
              {listProduct &&
                listProduct.length > 0 &&
                listProduct
                  .filter((item) => item.category === "smartphones")
                  .map((item, index) => {
                    return (
                      <div
                        className="item-category-page"
                        key={`product-${index}`}
                      >
                        <div className="img-product">
                          <Link to={`/${item.title}`}>
                            <img src={item.thumbnail} alt="" />
                          </Link>
                        </div>
                        <div className="name-product">
                          <h3>
                            <Link to={`/${item.title}`}>{item.title}</Link>
                          </h3>
                        </div>
                        <div className="number-price">
                          <span className="price-product">
                            {item.price} usd
                          </span>
                          <span className="discount">
                            -{item.discountPercentage}%
                          </span>
                        </div>
                      </div>
                    );
                  })}
            </div>
          </div>
          <div className="filter-all">
            <div className="filter-product">
              <div className="item-filter">
                <a href="">
                  <img src={filter1} alt="" />
                </a>
                <div className="title-filter">
                  <div className="title-child-filter">
                    <a href="">
                      Tm iPhone <br />
                      ph hp vi bn
                    </a>
                  </div>
                  <a href="" className="compare">
                    So snh cc iPhone 
                  </a>
                </div>
              </div>
              <div className="item-filter">
                <a href="">
                  <img src={filter2} alt="" />
                </a>
                <div className="title-filter">
                  <div className="title-child-filter">
                    <a href="">
                      Tm iPhone <br />
                      ph hp vi bn
                    </a>
                  </div>
                  <a href="" className="compare">
                    So snh cc iPhone 
                  </a>
                </div>
              </div>
            </div>
          </div>
          <div className="history-product">
            <p className="history-product-head">
              <h2>Lch s hnh thnh, pht trin ca iPhone</h2>
              <br />
            </p>
            <p className="history-product-body">
              iPhone l dng in thoi thng minh c pht trin t Apple Inc,
              c ra mt ln u tin bi Steve Jobs v m bn nm 2007. Bn
              cnh tnh nng ca mt my in thoi thng thng, iPhone cn
              c trang b mn hnh cm ng, camera, kh nng chi nhc v
              chiu phim, trnh duyt web... Phin bn th hai l iPhone 3G ra
              mt thng 7 nm 2008, c trang b thm h thng nh v ton
              cu, mng 3G tc  cao. Tri qua 15 nm tnh n nay  c n 34
              mu iPhone c sn xut t dng 2G cho n iPhone 13 Pro Max v
              Apple l mt trong nhng thng hiu in thoi c yu thch v
              s dng ph bin nht trn th gii.
            </p>
            <br />
            <br />
            <p className="history-product-head">
              <h2>iPhone c nhng m my no?</h2>
              <br />
            </p>
            <p className="history-product-body">
              Nhng chic iPhone do Apple phn phi ti th trng nc no th
              s mang m ca nc . V d: LL: M, ZA: Singapore, TH: Thi
              Lan, JA: Nht Bn, Nhng m ny xut hin ti Vit Nam u l hng
              xch tay, nhp khu. Cn ti Vit Nam, iPhone s c mang m
              VN/A. Tt c cc m ny u l hng chnh hng phn phi ca
              Apple. Li th khi bn s dng iPhone m VN/A  l ch  bo
              hnh tt hn vi 12 thng theo tiu chun ca Apple. iPhone ca
              bn s c bo hnh ti tt c cc trung tm bo hnh Apple ti
              Vit Nam, mt s m quc t b t chi bo hnh v phi em ra cc
              trung tm bo hnh Apple ti nc ngoi. Rt l phc tp ng
              khng no?
            </p>
            <br />
            <br />
            <p className="history-product-head">
              <h2>Apple  khai t nhng dng iPhone no?</h2>
              <br />
            </p>
            <p className="history-product-body">
              Tnh n nay, Apple  khai t (ngng sn xut) cc dng iPhone
              i c bao gm: iPhone 2G, iPhone 3G, iPhone 4, iPhone 5 series,
              iPhone 6 series, iPhone 7 series, iPhone 8 series, iPhone X
              series, iPhone SE (th h 1), iPhone SE (th h 2), iPhone 11 Pro,
              iPhone 11 Pro Max, iPhone 12 Pro, iPhone 12 Pro Max.
            </p>
            <br />
            <br />
          </div>
          <div className="rate-product">
            <div className="title-rate">
              <h2>nh gi danh mc</h2>
            </div>
            <div className="row">
              <Row className="row-res">
                <Col span={8} className="rate-first">
                  <div className="rate-all">
                    <div className="number-rate-all">
                      <h1>5</h1>
                      <p>6 nh gi</p>
                    </div>
                    <div className="percent-rate-all">
                      <div className="star-rate-all">
                        <p>5</p>
                        <img src={iconStar} alt="" />
                        <div className="progress">
                          <div
                            className="progress-bar"
                            aria-valuemin="0"
                            aria-valuemax="100"
                          ></div>
                        </div>
                      </div>
                      <div className="star-rate-all">
                        <p>4</p>
                        <img src={iconStar} alt="" />
                        <div className="progress">
                          <div
                            className="progress-bar"
                            aria-valuemin="0"
                            aria-valuemax="100"
                          ></div>
                        </div>
                      </div>
                      <div className="star-rate-all">
                        <p>3</p>
                        <img src={iconStar} alt="" />
                        <div className="progress">
                          <div
                            className="progress-bar"
                            aria-valuemin="0"
                            aria-valuemax="100"
                          ></div>
                        </div>
                      </div>
                      <div className="star-rate-all">
                        <p>2</p>
                        <img src={iconStar} alt="" />
                        <div className="progress">
                          <div
                            className="progress-bar"
                            aria-valuemin="0"
                            aria-valuemax="100"
                          ></div>
                        </div>
                      </div>
                      <div className="star-rate-all">
                        <p>1</p>
                        <img src={iconStar} alt="" />
                        <div className="progress">
                          <div
                            className="progress-bar"
                            aria-valuemin="0"
                            aria-valuemax="100"
                          ></div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="my-rate">
                    <strong>Vit nh gi ca ring bn</strong>
                    <div className="quality">
                      <p>Cht lng*:</p>
                      <span>
                        <img src={iconStar} alt="" />
                        <img src={iconStar} alt="" />
                        <img src={iconStar} alt="" />
                        <img src={iconStar} alt="" />
                        <img src={iconStar} alt="" />
                      </span>
                    </div>
                    <div className="form-input">
                      <label htmlFor="">Tn ca bn</label>
                      <Input />
                      <label htmlFor="">nh gi danh mc</label>
                      <Input />
                      <Button type="primary">Gi</Button>
                    </div>
                  </div>
                </Col>
                <Col span={16}>
                  <div className="user-comment">
                    <div className="comment">
                      <div className="profile-comment">
                        <img src={user} alt="" />
                        <p className="name-user">Nguyenvanphuong</p>
                        <p className="date-review">- 26/07/2023</p>
                      </div>
                      <span className="rate-star">
                        <img src={iconStar} alt="" />
                        <img src={iconStar} alt="" />
                        <img src={iconStar} alt="" />
                        <img src={iconStar} alt="" />
                        <img src={iconStar} alt="" />
                      </span>
                      <p>Ok</p>
                    </div>
                    <div className="comment">
                      <div className="profile-comment">
                        <img src={user} alt="" />
                        <p className="name-user">Nguyenvanphuong</p>
                        <p className="date-review">- 26/07/2023</p>
                      </div>
                      <span className="rate-star">
                        <img src={iconStar} alt="" />
                        <img src={iconStar} alt="" />
                        <img src={iconStar} alt="" />
                        <img src={iconStar} alt="" />
                        <img src={iconStar} alt="" />
                      </span>
                      <p>Ok</p>
                    </div>
                    <div className="comment">
                      <div className="profile-comment">
                        <img src={user} alt="" />
                        <p className="name-user">Nguyenvanphuong</p>
                        <p className="date-review">- 26/07/2023</p>
                      </div>
                      <span className="rate-star">
                        <img src={iconStar} alt="" />
                        <img src={iconStar} alt="" />
                        <img src={iconStar} alt="" />
                        <img src={iconStar} alt="" />
                        <img src={iconStar} alt="" />
                      </span>
                      <p>Ok</p>
                    </div>
                    <div className="comment">
                      <div className="profile-comment">
                        <img src={user} alt="" />
                        <p className="name-user">Nguyenvanphuong</p>
                        <p className="date-review">- 26/07/2023</p>
                      </div>
                      <span className="rate-star">
                        <img src={iconStar} alt="" />
                        <img src={iconStar} alt="" />
                        <img src={iconStar} alt="" />
                        <img src={iconStar} alt="" />
                        <img src={iconStar} alt="" />
                      </span>
                      <p>Ok</p>
                    </div>
                  </div>
                </Col>
              </Row>
            </div>
          </div>
          <div className="comment-post">
            <div className="title-comment">
              <h2>Bnh lun</h2>
            </div>
            <Row className="row-comment">
              <Col span={8} className="comment-first">
                <p className="title-form-comment">
                  <strong>Vit bnh lun ca bn</strong>
                </p>
                <div className="form-comment-post">
                  <div className="form-input">
                    <label htmlFor="">Tn ca bn</label>
                    <Input />
                    <label htmlFor="">Email hoc s in thoi</label>
                    <Input />
                    <label htmlFor="">Ni dung bnh lun</label>
                    <Input />
                    <Button type="primary">Gi</Button>
                  </div>
                </div>
              </Col>
              <Col span={16}>
                <div className="review">
                  <p className="notifi">
                    Hin cha c bnh lun no. Hy tr thnh ngi u tin
                    bnh lun !!!!
                  </p>
                </div>
              </Col>
            </Row>
          </div>
        </div>
      </div>
      <FooterComponent />
    </>
  );
};
export default IphoneComponent;
